> import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Create Application\", height: \"383px\", width: \"466px\" },\n        { templateName }\n    );\n}\n\nfunction changeView(view: any) {\n    viewData.value = view;\n    let tableVButton = document.getElementById(\"TableView\");\n    let cardVButton = document.getElementById(\"CardView\");\n    if (viewData.value == \"Table\") {\n        cardVButton?.classList.remove(\"activeView\");\n        tableVButton?.classList.add(\"activeView\");\n    } else if (viewData.value == \"Card\") {\n        tableVButton?.classList.remove(\"activeView\");\n        cardVButton?.classList.add(\"activeView\");\n    }\n}\n</script>\n\n<template>\n    <p class=\"app-text\">Create New Application</p>\n    <div class=\"createapp-container\">\n        <div class=\"text\"></div>\n        <div class=\"blankapp-logo-card\" @click=\"CreateApp()\">\n            <img src=\"../../../../images/blank-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n        </div>\n        <p class=\"title\">Blank Application</p>\n        <div class=\"blankapp-logo-card\" style=\"left: 211px\" @click=\"CreateApp('newAppTemplate1')\">\n            <img src=\"../../../../images/template-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n        </div>\n        <p class=\"title\" style=\"left: 226px\">Template Application</p>\n    </div>\n    <div class=\"filter-text-container\">\n        <span class=\"applications-text\">Your Domain Applications</span>\n        <div class=\"card-filters\">\n            <i class=\"mdi mdi-view-dashboard dashboard-icon activeView\" id=\"CardView\" @click=\"changeView('Card')\"></i>\n            <i class=\"mdi mdi-format-list-bulleted bulleted-icon\" id=\"TableView\" @click=\"changeView('Table')\"></i>\n        </div>\n    </div>\n    <div class=\"cards\">\n        <div class=\"row relative-for-children\">\n            <div\n                v-if=\"viewData == 'Card'\"\n                class=\"card-media\"\n                style=\"padding: 0px\"\n                v-for=\"app in viewModel.apps.sort((a, b) =>\n                    a.createDate && b.createDate ? (new Date(a.createDate).getTime() - new Date(b.createDate).getTime() > 0 ? -1 : 1) : 1\n                )\"\n                :key=\"app.ID\"\n                v-bind:value=\"app.ID\"\n            >\n                <ApplicationCard :app=\"app\" />\n            </div>\n            <div v-if=\"viewData == 'Table'\">\n                <AppDataTable />\n            </div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n* {\n    font-family: \"Roboto\";\n}\n\n.relative-for-children {\n    position: relative;\n    margin-left: 0px;\n}\n\n.blankapp-logo {\n    position: absolute;\n    top: calc((100% - 57px) / 2);\n    left: calc((100% - 57px) / 2);\n}\n\n.blankapp-logo-card {\n    width: 163px;\n    height: 119px;\n    border-radius: 4px !important;\n    position: absolute;\n    background: #eeeeee 0% 0% no-repeat padding-box;\n    border: 1px solid #d5d5d5;\n    top: 20px;\n    left: 24px;\n    cursor: pointer;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    background-color: #f5f5f5 !important;\n}\n\n.applications-text {\n    font-size: 22px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n}\n\n.app-text {\n    font-size: 22px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n    margin-top: 10px;\n}\n\n.createapp-container {\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n    width: 100%;\n    height: 187px;\n    min-width: 425px;\n    margin-top: 12px;\n}\n\n.title {\n    position: absolute;\n    top: 147px;\n    left: 48px;\n    color: #151233;\n    font-size: 15px;\n}\n\n.filter-text-container {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 35px;\n}\n\n.text {\n    position: absolute;\n    left: 24px;\n    top: 24px;\n}\n\n.dashboard-icon,\n.bulleted-icon {\n    font-size: 30px;\n    margin: 10px;\n    opacity: 0.5;\n    color: #151233;\n}\n\n.card-filters {\n    cursor: pointer;\n    margin-top: -12px;\n}\n\n.cards {\n    margin-top: 10px;\n}\n\n.activeView {\n    opacity: 1;\n}\n@media (min-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 8);\n    }\n}\n\n@media (max-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 7);\n    }\n}\n\n@media (max-width: 2150px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 6);\n    }\n}\n\n@media (max-width: 1820px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 5);\n    }\n}\n\n@media (max-width: 1550px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 4);\n    }\n}\n\n@media (max-width: 1250px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 3);\n    }\n}\n\n@media (max-width: 950px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 2);\n    }\n}\n\n@media (max-width: 700px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: 100%;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
